/*===== Separaciones =====*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-image: linear-gradient(90deg, hsl(197deg, 92%, 66%) 0%, hsl(179deg, 74%, 57%) 50%, hsl(166deg, 94%, 51%) 100%);
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.no-seleccionable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.sombra {
  -webkit-box-shadow: 8px 8px 21px -4px rgb(66, 68, 90);
  -moz-box-shadow: 8px 8px 21px -4px rgb(66, 68, 90);
  box-shadow: 8px 8px 21px -4px rgb(66, 68, 90);
}

.ola {
  position: absolute;
  z-index: -1000;
  width: 100%;
}
.ola img {
  height: 100vh;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.header h2, .header h3 {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  color: #fff;
}
.header h2 {
  margin-top: 4rem;
  font-family: "Tiro Devanagari Hindi", serif;
}
@media (min-width: 480px) {
  .header h2 {
    font-size: 8rem;
  }
  .header h3 {
    font-size: 2rem;
  }
}
@media (min-width: 769px) {
  .header h2 {
    font-size: 15rem;
  }
  .header h3 {
    font-size: 4rem;
    transform: translateY(-5rem);
  }
}

.seccion-valores {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  transform: translateY(18rem);
  text-align: justify;
}
.seccion-valores .titulo-valor {
  text-transform: capitalize;
  color: #490086;
  font-weight: 700;
}
.seccion-valores .valores-imagen {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.seccion-valores .valores-imagen img {
  width: 50rem;
  height: 50rem;
}
.seccion-valores .numero-valor-nombre {
  display: flex;
  align-items: center;
  text-transform: capitalize;
}
.seccion-valores .numero-valor-nombre .num-valor {
  position: relative;
  color: #fff;
  background-color: #490086;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
}

.seccion-productos {
  transform: translateY(16rem);
  background-color: #fff;
  padding-top: 4rem;
}
.seccion-productos h3 {
  text-transform: capitalize;
  font-weight: 700;
  color: #490086;
}
.seccion-productos .galeria {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
}
.seccion-productos .galeria img {
  height: 100%;
}

.colaboradores {
  padding-top: 4rem;
  background-color: #fff;
  transform: translateY(16rem);
  padding-bottom: 3rem;
}
.colaboradores h3 {
  text-transform: capitalize;
  color: #490086;
  font-weight: 700;
}
.colaboradores .tarjeta {
  background-color: #490086;
  border-radius: 1.5rem;
  width: 30rem;
  height: 50rem;
}
.colaboradores .tarjeta img {
  margin: auto;
  transform: translateY(2rem);
  width: 20rem;
  border-radius: 50%;
}
.colaboradores .tarjeta h3 {
  transform: translateY(2rem);
  color: #fff;
  font-weight: 700;
}
.colaboradores .tarjetas {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.seccion-historia {
  transform: translateY(13rem);
  background-color: #F2F2F2;
  padding-bottom: 2rem;
}
.seccion-historia h3 {
  padding-top: 3rem;
  color: #490086;
  font-weight: 700;
  text-transform: capitalize;
}
.seccion-historia img {
  width: 40rem;
  margin: 0 auto;
}

footer {
  transform: translateY(11rem);
  background-color: #3F3F3F;
  height: 50rem;
}
footer .contenido-footer .datosFooter {
  display: flex;
  justify-content: space-between;
}