@use "variables" as v;

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: v.$fuente-principal;
  font-size: 1.6rem;
  line-height: 1.8;
  background-image: linear-gradient(
                  90deg,
                  hsl(197deg 92% 66%) 0%,
                  hsl(179deg 74% 57%) 50%,
                  hsl(166deg 94% 51%) 100%
  );
}
p {
  color: v.$negro;
  font-size: 2rem;
}
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: v.$fuente-principal;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;

}

h1 {
  font-size: 3.8rem;
}
h2 {
  font-size: 3.4rem;
}
h3 {
  font-size: 3rem;
}
h4 {
  font-size: 2.6rem;
}

.no-seleccionable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}